<template lang="pug">
    section(class="contact")
        div(class="container")
            div(class="title")
                h2 contact
                    span us
            div(class="contact-info")
                div(class="row")
                    div(class="col-md-6 col-lg-6")
                        div(class="info-content wow fadeInUp" data-wow-duration="1s")
                            div(class='info-box')
                                fa(class="theicon" :icon="['fa', 'map-marker-alt']")
                                span Address
                                p {{ ContactData.address }}
                            div(class='info-box')
                                fa(class="theicon" :icon="['fas', 'envelope']")
                                span Email
                                p
                                    a(class="d-block" v-for="singleEmail in ContactData.email", :key="singleEmail.id" :href="'mailto:' + singleEmail") {{ singleEmail }}
                            div(class='info-box')
                                fa(class="theicon" :icon="['fas', 'phone-alt']")
                                span phone
                                p
                                    a(:href="'tel:' + ContactData.phone") {{ ContactData.phone }}
                            div(class='info-box')
                                fa(class="theicon" :icon="['fab', 'whatsapp']")
                                span WhatsApp
                                p 
                                    a(:href="'https://api.whatsapp.com/send?l=ar&amp;phone=' +ContactData.phone+'&amp;text=&amp;source=&amp;data='") {{ ContactData.whatsapp }}
                    div(class="col-md-6 col-lg-6")
                        .branchs 
                            .branch
                                .icon 
                                    img(src="@/assets/images/icons/01.png", alt="")
                                .text 
                                    h3 Iraq branch
                                    p
                                        a(:href="'tel:'+ ContactData.iraq_phone") {{ ContactData.iraq_phone }}
                            .branch
                                .icon 
                                    img(src="@/assets/images/icons/02.png", alt="")
                                .text 
                                    h3 Kuwait branch
                                    p
                                        a(:href="'tel:'+ ContactData.kuwait_phone") {{ ContactData.kuwait_phone }}
        div(class="contact-form")
            div(class="container")
                div(class="row")
                    div(class="col-md-12 col-lg-6")
                        .map
                            div(class="subtitle")
                                h3 location
                            iframe(:src="ContactData.map" style="border:0;" allowfullscreen="" loading="lazy")
                    div(class="col-md-12 col-lg-6")
                        div(class='form-area')
                            div(class="subtitle")
                                h3 Leave your message
                            form(@submit="postFormData" ref="main_form" action='')
                                .row
                                    div(class="col-lg-6")
                                        div(class='form-group')
                                            input(placeholder='First name' class='form-control' type='text' name="first_name" v-model="formData.first_name")
                                    div(class="col-lg-6")
                                        div(class='form-group')
                                            input(placeholder='Last name' class='form-control' type='text' name="last_name" v-model="formData.last_name"  required)
                                    div(class="col-lg-12")            
                                        div(class='form-group')
                                            input(placeholder='Email' class='form-control' type='email' name="email" v-model="formData.email" required)
                                    div(class="col-lg-12")            
                                        div(class='form-group')
                                            input(placeholder='Phone number' class='form-control' type='text' name="phone" v-model="formData.phone" required)
                                    div(class="col-lg-12")            
                                        div(class='form-group')
                                            textarea(rows='6' placeholder='Message...' class='form-control' name="message" v-model="formData.message" required)
                                    div(class="col-lg-12")               
                                        div(class='form-group subm')
                                            button(class='btn btn-danger hvr-sweep-to-bottom' type='submit') Send
                                    //- div(class="col-lg-12")               
                                    //-     div(class='text-center')
                                    //-         p(class='successfully') Message Sent Successfully 
</template>

<script>
import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";
import { mapState } from "vuex";

Vue.use(VueAxios, axios);

export default {
    name: "TheContact",
    data() {
        return {
            formData: {
                first_name: null,
                last_name: null,
                email: null,
                phone: null,
                message: null,
            },
        };
    },
    methods: {
        postFormData(e) {
            console.warn(this.formData);
            e.preventDefault();
            this.axios
                .post(
                    "https://newpcloud.com/perfect/public/api/contact-store",
                    this.formData
                )
                .then((result) => {
                    console.warn(result);
                });
            this.$refs.main_form.reset();
        },
    },
    mounted() {
        this.$store.dispatch("getContactData");
    },
    computed: {
        ...mapState(["ContactData"]),
    },
};
</script>
